@mixin rwd($bp) {
    @if map-has-key($breakpoints, $bp){
        @media screen and (min-width: map-get($breakpoints, $bp)) {
            @content;
        }
    }@else{
        @media screen and (min-width: $bp) {
            @content
        }
    }
}

@mixin multimedia($w, $h) {
    display: grid;
    grid-template-columns: repeat($w, 1fr);
    grid-template-rows: repeat($h, 1fr);
    position: relative;
        &::before {
            content: '';
            display: block;
            padding-bottom: 100%;
        }
}

@mixin button($color, $display, $color__segundo) {
    background-color: $color;
    display: $display;
    color: $color__segundo;
    font-size: 1.5rem;
    font-weight: 700;
    border: none;
    border-radius: .5rem;
    text-decoration: none;
    padding: .8rem 3rem;
    text-align: center;

    &:hover {
        background-color: darken($color, 10);
        cursor: pointer;
    }

    &:active {
        transform: scale(.9);
    }
}