/* PAGINA//CARTAS */
.carta
{
    margin-bottom: 5rem;

    &::after {
        content: '';
        display: block;
        width: 50%;
        height: .1rem;
        background-color: var(--negro);
        transform: translateX(50%);
    }
}


.enlaces
{
    width: 15%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: .3rem;
    margin-bottom: 2rem;

    .enlaces__icon {
        color: var(--blanco);
        font-size: 2.8rem;
    }

    .enlace--icon {
        transform: rotate(-90deg);
    }
}