/* BARRA LATERAL MENU */
.navbar
{
    position: fixed;
    top: 0;
    left: 0;
    width: $nav-width / 2;
    height: 100vh;
    background-color: $ventanaOne;
    backdrop-filter: blur(20px);
    border-right: .1rem solid $ventanaFour;
    color: var(--negro);
    padding: .5rem;
    transition: .5s;
    z-index: $z-fixed;

    @include rwd(s) {
        width: $nav-width;
        padding: 2rem;
    }
}

.nav
{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;
}

.nav__brand
{
    visibility: hidden;
    
    @include rwd(l) {
        display: grid;
        grid-template-columns: max-content max-content;
        justify-content: space-between;
        align-content: center;
        margin-bottom: 2rem;
        visibility: visible;
    }
}

.nav__toggle
{
    font-size: 3rem;
    padding: 1rem;
    cursor: pointer;
}

.nav__logo
{
    color: var(--negro);
    font-weight: 600;
}

.nav__link
{
    display: grid;
    grid-template-columns: max-content max-content;
    align-items: center;
    column-gap: 1rem;
    padding: 1rem;
    color: var(--negro);
    border-radius: .5rem;
    margin-bottom: 1rem;
    transition: .3s;
    cursor: pointer;
    position: relative;

    &:hover {
        background-color: $ventanaThree;
        border: .1rem solid $ventanaFour;
        backdrop-filter: blur(20px);
    }
}

.nav__icon
{
    font-size: 2rem;

    @include rwd(s) {
        font-size: 3rem;
    }
}

.nav__name
{
    font-size: 1rem;

    @include rwd(s) {
        font-size: 1.5rem;
    }
}

.expander
{
    width: $nav-width + 5rem;

    @include rwd(s) {
        width: $nav-width + 10rem;
    }
}

.active
{
    background-color: $ventanaThree;
    border: .1rem solid $ventanaFour;
    backdrop-filter: blur(20px);
}

.body-pd
{
    padding: 0 0 0 14rem;

    @include rwd(s) {
        padding: 0 0 0 16rem;
    }
}

.notif
{
    &::before {
        content: '1';
        display: inline-block;
        position: absolute;
        width: 2.2rem;
        height: 2.2rem;
        border-radius: 50%;
        border: .1rem solid;
        padding: .3rem;
        right: .1rem;
        top: -.5rem;
        color: var(--scrollOne);
        font-weight: 700;
    }
}