/* FORMULARIO*/
.body--form
{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    position: relative;
}

.fomulario
{
    background-color: $ventanaOne;
    backdrop-filter: blur(20px);
    border: .1rem solid $ventanaFour;
    -webkit-box-shadow: 7px 10px 14px 1px #000000; 
    box-shadow: 7px 10px 14px 1px #000000;
    display: grid;

    @include rwd(l) {
        grid-template-columns: 1fr 2fr;
    }
}

.form__images
{
    display: none;
    
    @include rwd(l) {
        display: flex;
        background-image: img('torre_tokyo.jpg');
        background-position: center center;
        background-size: cover;
        background-repeat: no-repeat;
        width: 30rem;
    }
}

.form__contenido
{
    padding: 3rem 1rem;
}

.campo
{
    margin: .5rem;
    padding: 1rem;

    .campo__field {
        width: 100%;
        border-style: none;
        border: none;
        padding: .6rem;
        background-color: transparent;

        &--textarea {
            height: 20rem;
            resize: none;
        }

        &::placeholder {
            color: var(--negro);
        }

        &:focus {
            outline: none;
            border-bottom: .1rem solid var(--color-cuarto);
            color: var(--color-primero);
        }
    }

    & input[type="file"] {
        display: none;
    }
    .campo__fiel {
        padding: 1rem 3rem 0;
        font-weight: 700;
        font-size: 1.4rem;
        border-bottom: .1rem solid;
        transition: all .5s ease;

        &:hover {
            border: none;
            transition: all .5s ease;
            color: var(--color-primeroDark);
        }
    }

    h3 {
        text-align: center;
        margin: 0;

        span {
            background-color: var(--color-segundo);
            padding: .3rem;
            border-radius: .5rem;
            color: var(--blanco);
        }
    }
}

.campo__vista--imagen
{
    padding-top: 1rem;
}

.botones
{
    display: flex;
    width: 100%;
    justify-content: space-between;
    margin-top: 1rem;

    @include rwd(l) {
        width: 50%;
    }
}

.form__btn
{
    position: absolute;
    top: 0;
    right: 0;
    border: none;
    background: none;

    @include rwd(xl) {
        top: 1rem;
        right: 1rem;
    }

    &:hover {
        border: none;
        background: none;
    }
}