/* MULTIMEDIA */
.box
{
    height: 74rem;
    padding: 0 2rem;
    border-radius: 50px;
    background: linear-gradient(145deg, #e3e3e3, #ffffff);
    box-shadow:  20px 20px 60px #d6d6d6,
                -20px -20px 60px #ffffff;
    @include rwd(m) {
        height: 62rem;
    }
    @include rwd(l) {
        height: 50rem;
    }
}

.box__boton
{   
    ul {
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: 700;
        flex-direction: column;
        padding: 0;
        @include rwd(l) {
            flex-direction: row;
            justify-content: space-around;
        }
    }

    .li--icon {
        opacity: 0;
        color: var(--color-primeroDark);
    }

    .li {
        padding: 1rem;
        cursor: pointer;
        font-size: 2rem;
        @include rwd(l) {
            padding-bottom: 0;
        }

        &.activo {
            color: var(--color-primeroDark);

            &::after {
                content: '';
                display: block;
                transform: translateX(50%);
                width: 50%;
                height: .2rem;
                background-color: var(--color-primero);
            }

            & .li--icon {
                opacity: 1;
            }
        }

        &:hover {
            color: var(--color-primeroDark);
            transition: color .5s ease;
        }

        .li a:visited {
            color: var(--color-primeroDark);
        }
    }
}

.box__content
{
    position: relative;
}

.box__context
{
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 3rem 1rem 0;
    position: absolute;
    opacity: 0;

    &.activo {
        // background-color: turquoise;
        // border: 1px solid;
        // border-radius: 10px;
        opacity: 1;
        // height: 40rem;
    }

    @include rwd(l) {
        flex-direction: row;
    }

    .box__img {
        text-align: center;

        img {
            width: 70%;
            border-radius: 1rem;
        }
    }

    .box__text {
        font-weight: 700;
        text-align: center;
        font-size: 2rem;

        p:first-child {
            font-size: 3rem;
            margin: 1rem 0;
            color: var(--color-primero);
            @include rwd(m) {
                font-size: 3rem;
            }
        }
    }
}
