.dark-mode
{
    background-color: hsla(0, 0%, 0%, 0.9);

    .navbar {
        background-color: $ventanaThree;
        border-right: .1rem solid $ventanaTwo;
        color: var(--blanco);
    }

    .nav__logo {
        color: var(--blanco);
    }

    .nav__link {
        color: var(--blanco);

        &:hover {
            background-color: $ventanaOne;
            border: .1rem solid $ventanaTwo;
            backdrop-filter: blur(20px);
        }
    }

    .active {
        background-color: $ventanaOne;
        border: .1rem solid $ventanaTwo;
        backdrop-filter: blur(20px);
    }

    h1,
    h2,
    h3,
    p,
    li {
        color: var(--blanco);
    }

    .name {
        color: var(--color-primero);
        border-bottom: .1rem solid var(--color-primero);
    }

    .sing {
        background-image: img('sky_night.jpg');
    }
    
    .box {
        border-radius: 50px;
        background: linear-gradient(145deg, #222222, #292929);
        box-shadow:  20px 20px 60px #202020,
                    -20px -20px 60px #2c2c2c;
    }

    iframe {
        border: 1px solid var(--color-segundo);
    }

    .footer {

        h3 {
            color: var(--negro);
        }
        background-color: var(--blanco);
        color: var(--negro);
    }

    .carta {
        &::after {
            background-color: var(--blanco);
        }
    }

    .galeria__contenido {
        background-color: rgba(0, 0, 0, 0.5);
    }

    .galeria__icon,
    .galeria__copy,
    .goup__icon {
        color: var(--blanco);
    }

    .fechas {
        a {
            color: var(--color-primeroDark);
        }
    }

    .fomulario {
        background-color: $ventanaTwo;
        backdrop-filter: blur(20px);
        border: .1rem solid $ventanaThree;
        -webkit-box-shadow: 7px 10px 14px 1px #F4F4F4; 
        box-shadow: 7px 10px 14px 1px #F4F4F4;
    }

    .form__images {
        background-image: img('torre_tokyo_night.jpg');
    }

    .campo h3 span {
        color: var(--negro);
    }

    .campo__field::placeholder {
        color: var(--blanco);
    }

    .form__btn {
        background: none;
        border: none;

        &:hover {
            background: none;
            border: none;
        }
    }
}