html
{
    box-sizing: border-box;
    font-size: 62.5%;
    scroll-behavior: smooth;
}

*, *::before, *::after
{
    box-sizing: inherit;
}

body
{
    font-size: 1.6rem;
    line-height: 1.5;
    font-family: var(--fuenteText);
    position: relative;
    transition: .5s;
    line-height: 1.5;
}

img
{
    max-width: 100%;
}

a
{
    text-decoration: none;
}

li
{
    list-style: none;
}