/* PAGES//ANECDOTARIO */
.contenido
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    @include rwd(l) {
        display: grid;
        grid-template-columns: 1fr 2fr;
        text-align: center;
        gap: 5rem;
    }
}

.bloque
{
    h3 {
        cursor: pointer;
    }

    p {
        height: 0;
        padding: 0;
        overflow: hidden;
        transition: all .5s ease;
    }

    a {
        display: inline-block;
        font-weight: 700;
        padding: .5rem;
        margin: .3rem;
        border-radius: 1rem;
        color: var(--color-cuarto);
        transition: background-color .5s ease, color .5s ease;

        &:hover {
            background-color: var(--color-segundo);
            color: var(--blanco);
        }
    }

    &.activo p {
        height: 5rem;
    }
}

.link
{
    color: var(--color-cuarto);
    padding: .5rem;
    margin: .3rem;
    border-radius: 1rem;
    transition: background-color .5s ease, color .5s ease;

    &:hover {
        background-color: var(--color-segundo);
        color: var(--blanco);
    }
}

.vector
{
    width: 100%;

    img {
        width: 80%;
    }
}