/* HEADER // BANNER */
.header
{
    display: grid;
    grid-template-columns: 1fr;
    align-items: center;
    width: 100%;

    @include rwd(l) {
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
        align-items: center;
    }
}

.texto
{
    text-align: center;

    h1,
    p {
        font-family: var(--fuenteHeading);
    }

    h1 {
        font-size: 2.5rem;

        @include rwd (s) {
            font-size: 5rem;
        }
    }

    p {
        font-size: 1.5rem;

        @include rwd (s) {
            font-size: 2rem;
        }
    }
}

.name
{
    color: var(--color-cuarto);
    border-bottom: .1rem solid var(--color-cuarto);
}