/* PAGINA//GALERIA */
.galeria
{
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(200px,1fr));
    justify-items: center;
    justify-self: center;
}

.galeria__img
{
    width: 90%;
    height: 90%;
    object-fit: contain;

    &:hover
    {
        filter: grayscale(50%);
        cursor: pointer;
        transition: filter .5s;
    }
}

.galeria__contenido
{
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    transform: translateX(-100%);
    background-color: rgba(255, 255, 255, 0.2);
    display: flex;
    justify-content: center;
    align-items: center;
    transition: transform .4s ease-in;
}

.mover
{
    transform: translateX(0);
}

.galeria__show
{
    height: 50%;
    transform: scale(0);
    display: block;
    object-fit: cover;
    transition: transform .5s .4s;

    @include rwd(m) {
        height: 70%;
    }
}

.show
{
    transform: scale(1);
}

.galeria__icon
{
    position: absolute;
    color: var(--negro);
    top: 1rem;
    right: 1rem;
    font-size: 2.5rem; 
    cursor: pointer;

    @include rwd(m) {
        top: 2rem;
        right: 2rem;
        font-size: 3.5rem; 
    }
}

.galeria__copy
{
    position: fixed;
    color: var(--negro);
    bottom: 8rem;
    font-size: 1.5rem;
    border-bottom: 1px solid;

    @include rwd(m) {
        bottom: 4rem;
        font-size: 2rem;
    }
}