      backdrop-filter: blur(20px); }
  .dark-mode .active {
    background-color: rgba(255, 255, 255, 0.1);
    border: 0.1rem solid rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(20px); }
  .dark-mode h1,
  .dark-mode h2,
  .dark-mode h3,
  .dark-mode p,
  .dark-mode li {
    color: var(--blanco); }
