  .dark-mode .fomulario {
    background-color: rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(20px);
    border: 0.1rem solid rgba(0, 0, 0, 0.4);
    -webkit-box-shadow: 7px 10px 14px 1px #F4F4F4;
    box-shadow: 7px 10px 14px 1px #F4F4F4; }
  .dark-mode .form__images {
    background-image: url("../../images/torre_tokyo_night.jpg"); }
  .dark-mode .campo h3 span {
    color: var(--negro); }
  .dark-mode .campo__field::placeholder {
    color: var(--blanco); }
  .dark-mode .form__btn {
    background: none;
    border: none; }
    .dark-mode .form__btn:hover {
      background: none;
      border: none; }

html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth; }

*, *::before, *::after {
  box-sizing: inherit; }

body {
  font-size: 1.6rem;
  line-height: 1.5;
  font-family: var(--fuenteText);
  position: relative;
  transition: .5s;
  line-height: 1.5; }

img {
  max-width: 100%; }

a {
  text-decoration: none; }

li {
  list-style: none; }

.container {
  max-width: 120rem;
  width: 60%;
  margin: 0 auto;
  padding: 0 2rem; }
  @media screen and (min-width: 1024px) {
    .container {
      width: 85%; } }

.centrar {
  text-align: center; }

.title__pages {
  font-size: 3.5rem; }

.subtitle__pages {
  font-size: 2rem; }

.boton {
  background-color: transparent;
  display: inline-block;
  color: var(--negro);
  font-size: 1.5rem;
  font-weight: 700;
  border: none;
  border-radius: .5rem;
  text-decoration: none;
  padding: .8rem 3rem;
  text-align: center; }
  .boton:hover {
    background-color: rgba(0, 0, 0, 0);
    cursor: pointer; }
  .boton:active {
    transform: scale(0.9); }

/* DISEÃ‘O */
/* BARRA LATERAL MENU */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 4.5rem;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(20px);
  border-right: 0.1rem solid rgba(0, 0, 0, 0.5);
  color: var(--negro);
  padding: .5rem;
  transition: .5s;
  z-index: 100; }
  @media screen and (min-width: 376px) {
    .navbar {
      width: 9rem;
      padding: 2rem; } }

.nav {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow: hidden; }

.nav__brand {
  visibility: hidden; }
  @media screen and (min-width: 1024px) {
    .nav__brand {
      display: grid;
      grid-template-columns: max-content max-content;
      justify-content: space-between;
      align-content: center;
      margin-bottom: 2rem;
      visibility: visible; } }

.nav__toggle {
  font-size: 3rem;
  padding: 1rem;
  cursor: pointer; }

.nav__logo {
  color: var(--negro);
  font-weight: 600; }

.nav__link {
  display: grid;
  grid-template-columns: max-content max-content;
  align-items: center;
  column-gap: 1rem;
  padding: 1rem;
  color: var(--negro);
  border-radius: .5rem;
  margin-bottom: 1rem;
  transition: .3s;
  cursor: pointer;
  position: relative; }
  .nav__link:hover {
    background-color: rgba(0, 0, 0, 0.4);
    border: 0.1rem solid rgba(0, 0, 0, 0.5);
    backdrop-filter: blur(20px); }

.nav__icon {
  font-size: 2rem; }
  @media screen and (min-width: 376px) {
    .nav__icon {
      font-size: 3rem; } }

.nav__name {
  font-size: 1rem; }
  @media screen and (min-width: 376px) {
    .nav__name {
      font-size: 1.5rem; } }

.expander {
  width: 14rem; }
  @media screen and (min-width: 376px) {
    .expander {
      width: 19rem; } }

.active {
  background-color: rgba(0, 0, 0, 0.4);
  border: 0.1rem solid rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(20px); }

.body-pd {
  padding: 0 0 0 14rem; }
  @media screen and (min-width: 376px) {
    .body-pd {
      padding: 0 0 0 16rem; } }

.notif::before {
  content: '1';
  display: inline-block;
  position: absolute;
  width: 2.2rem;
  height: 2.2rem;
  border-radius: 50%;
  border: .1rem solid;
  padding: .3rem;
  right: .1rem;
  top: -.5rem;
  color: var(--scrollOne);
  font-weight: 700; }

/* HEADER // BANNER */
.header {
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
  width: 100%; }
  @media screen and (min-width: 1024px) {
    .header {
      grid-template-columns: 2fr 1fr;
      column-gap: 2rem;
      align-items: center; } }

.texto {
  text-align: center; }
  .texto h1,
  .texto p {
    font-family: var(--fuenteHeading); }
  .texto h1 {
    font-size: 2.5rem; }
    @media screen and (min-width: 376px) {
      .texto h1 {
        font-size: 5rem; } }
  .texto p {
    font-size: 1.5rem; }
    @media screen and (min-width: 376px) {
      .texto p {
        font-size: 2rem; } }

.name {
  color: var(--color-cuarto);
  border-bottom: 0.1rem solid var(--color-cuarto); }

/* LETRERO */
.sing {
  width: 100%;
  text-align: center;
  background-image: url("../../images/sky.jpg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 5rem 0;
  margin-bottom: 2rem;
  z-index: -1; }

.sing__pg {
  font-size: 4rem;
  margin: 0;
  position: relative;
  display: inline-block;
  animation: moveOne 15s infinite linear; }
  @media screen and (min-width: 376px) {
    .sing__pg {
      animation: moveTwo 15s infinite linear; } }
  @media screen and (min-width: 768px) {
    .sing__pg {
      animation: moveThree 15s infinite linear; } }
  @media screen and (min-width: 1024px) {
    .sing__pg {
      animation: moveFour 15s infinite linear; } }

@keyframes moveOne {
  from {
    left: 376px; }
  to {
    left: -4800px; } }

@keyframes moveTwo {
  from {
    left: 768px; }
  to {
    left: -4800px; } }

@keyframes moveThree {
  from {
    left: 1024px; }
  to {
    left: -4800px; } }

@keyframes moveFour {
  from {
    left: 1280px; }
  to {
    left: -4800px; } }

/* MULTIMEDIA */
.box {
  height: 74rem;
  padding: 0 2rem;
  border-radius: 50px;
  background: linear-gradient(145deg, #e3e3e3, #ffffff);
  box-shadow: 20px 20px 60px #d6d6d6, -20px -20px 60px #ffffff; }
  @media screen and (min-width: 768px) {
    .box {
      height: 62rem; } }
  @media screen and (min-width: 1024px) {
    .box {
      height: 50rem; } }

.box__boton ul {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  flex-direction: column;
  padding: 0; }
  @media screen and (min-width: 1024px) {
    .box__boton ul {
      flex-direction: row;
      justify-content: space-around; } }

.box__boton .li--icon {
  opacity: 0;
  color: var(--color-primeroDark); }

.box__boton .li {
  padding: 1rem;
  cursor: pointer;
  font-size: 2rem; }
  @media screen and (min-width: 1024px) {
    .box__boton .li {
      padding-bottom: 0; } }
  .box__boton .li.activo {
    color: var(--color-primeroDark); }
    .box__boton .li.activo::after {
      content: '';
      display: block;
      transform: translateX(50%);
      width: 50%;
      height: .2rem;
      background-color: var(--color-primero); }
    .box__boton .li.activo .li--icon {
      opacity: 1; }
  .box__boton .li:hover {
    color: var(--color-primeroDark);
    transition: color .5s ease; }
  .box__boton .li .li a:visited {
    color: var(--color-primeroDark); }

.box__content {
  position: relative; }

.box__context {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 3rem 1rem 0;
  position: absolute;
  opacity: 0; }
  .box__context.activo {
    opacity: 1; }
  @media screen and (min-width: 1024px) {
