      flex-direction: row; } }
  .box__context .box__img {
    text-align: center; }
    .box__context .box__img img {
      width: 70%;
      border-radius: 1rem; }
  .box__context .box__text {
    font-weight: 700;
    text-align: center;
    font-size: 2rem; }
    .box__context .box__text p:first-child {
      font-size: 3rem;
      margin: 1rem 0;
      color: var(--color-primero); }
      @media screen and (min-width: 768px) {
        .box__context .box__text p:first-child {
          font-size: 3rem; } }

/* VIDEOS YOUTUBE */
.videos {
