  display: flex;
  position: fixed;
  bottom: .2rem;
  right: .2rem;
  width: 4rem;
  height: 4rem;
  z-index: -1; }

.goup__content {
  width: 0rem;
  height: 0rem;
  border-radius: 50%;
  background: #008080;
  box-shadow: inset -5px -5px 10px #003333,  inset 5px 5px 10px #00cdcd;
  -webkit-box-shadow: -2px -3px 17px 3px #551A8B;
  box-shadow: -2px -3px 17px 3px #551A8B;
  transition: .2s;
  top: 50%;
  left: 50%;
