/* ENTRADAS ANECDOTARIO */
.body-note
{
    background-color: var(--bg-nota);
    color: var(--color-nota);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 2rem;

    @include rwd(l) {
        padding: 1rem 5rem;
    }
}

.note
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
    @include rwd(l) {
        display: grid;
        grid-template-columns: 1fr 2fr;
        grid-template-rows: 10rem 50rem;
        justify-content: center;
        align-items: center;
        gap: 2rem;
    }
}

.note__title
{
    h2 {
        font-size: 2.8rem;
    }   

    @include rwd(s) {
        grid-column: span 2;
        padding: 0 3rem;
        h2 {
            font-size: 4rem;
        }
    }
}

.note__images
{
    display: flex;
    justify-content: center;
    align-items: center;

    .nt--img
    {
        width: 70%;
    }
}

.note__content
{
    text-align: center;

    ul {
        padding: 0;
    }

    p {
        font-size: 2rem;

        span {
            font-weight: 700;
        }

        strong {
            color: var(--color-segundo);
            font-weight: 700;
        }
    }

    .nt__ico {
        font-size: 3rem;
        color: inherit;

        &:visited {
            color: inherit;
        }
    }
}